<template>
  <div class="box">
    <h1>找回密码</h1>
    <!-- :rules="rules"先不弄规则 -->
    <el-form :model="ruleForm" status-icon ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="用户名" prop="username" class="use">
        <el-input
          type="text"
          v-model="ruleForm.username"
          ref="username"
          autocomplete="off"
          width="120px"
          @change="cun"
        ></el-input>
      </el-form-item>
      <el-form-item label="手机号码" prop="phone" class="use">
        <el-input
          type="text"
          v-model.number="ruleForm.phone"
          ref="phone"
          autocomplete="off"
          width="120px"
        ></el-input>
      </el-form-item>

      <el-form-item label="验证码" prop="yan" class="zheng bai">
        <!-- <el-row :gutter="20"> -->
        <el-col :span="19" class="cols">
          <el-input type="text" v-model="ruleForm.yan" ref="yans"></el-input>
        </el-col>
        <el-col :span="4" class="bag">
          <button
            style="width:85px;height:40px"
            @click="sendcode"
          >{{num > 0 ? '倒计时'+num+'秒' : '获取验证码'}}</button>
          <!-- <button @click="getPhoneVcode()" ref="jin" class="yan">发送验证码</button> -->
        </el-col>
        <!-- </el-row> -->
      </el-form-item>
      <el-form-item label="新密码" prop="newPass" class="new">
        <el-input
          type="password"
          v-model="ruleForm.newPass"
          ref="newpass"
          autocomplete="off"
          width="120px"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm()">修改</el-button>
        <a @click="Login" class="wang">回到登录</a>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
