    //     callback();
    //   }
    // };

    return {
      userid: "",
      btnval: "发送验证码",
      num: -1,
      ruleForm: {
        username: "",
        // oldpass: "",
        newPass: "",
        yan: "",
        phone: "",
      },
      // rules: {
      //   phone: [
      //     { required: true, message: "手机号码必填", trigger: "change" },
      //     { validator: checkphone, trigger: "change" },
