    };
  },
  methods: {
    sendcode(ev) {
      console.log(123);
      console.log(ev.target);
      //發送請求
      // if (this.ruleForm.phone != "") {
      //   this.$refs["ruleForm"].validate(async () => {
      ev.target.disabled = true; //點擊第一次就發送請求
      this.num = 60;
      let times = 0;
      let timer = setInterval(async () => {
        let num = this.num;
        this.num = --num;
        console.log(this.num, 899); //获取验证码的代码
        const { username } = this.ruleForm;
        const { phone } = this.ruleForm;

        const { data } = await this.$request.get(
