        ); //获取验证码存到数据库
        //返回的数据是发送成功
        console.log("返回的数据", data);
        times++;
        if (times == 60) {
          ev.target.disabled = false; //點擊第一次就發送請求
          this.num = -1;
          clearInterval(timer);
        }
      }, 1000);
