      //这里是如果规则通过才会调用
      this.$refs["ruleForm"].validate(async (valid) => {
        if (valid) {
          const { username } = this.ruleForm;
          const { data } = await this.$request.post("/phonevcode/?username", {
            username,
          });
          if (data.code === 1) {
            this.$message({
              type: "success",
              message: "用户名正确",
            });
          } else {
            this.$message({
              type: "error",
              message: "请输入正确的用户名",
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //获取手机验证码
    // async getPhoneVcode() {
    // clearInterval(t);
    //点击之后60秒内会发出一个验证码
    // setInterval(() => {
    //         this.$refs.jin.disabled=true
    // }, 2000);
    // const { username } = this.ruleForm;
    // const { phone } = this.ruleForm;
    // const { data } = await this.$request.get(
    //   `/phonecheck/check/?phone=${phone}&username=${username}`
    // );
    // console.log(data);
    //  const vv=this.$refs.code.disabled
    //  console.log(vv);
    // if (this.ruleForm.phone != "") {
    //   this.$refs["ruleForm"].validate(async () => {
    //     console.log("yyy", t);
    //     console.log(345);
    //     var t = null;
    //     t = setTimeout(async () => {
    //       console.log(123);
    //       clearInterval(t);
    //       console.log("手机号码", phone);
    //       const { data } = await this.$request.get(
    //         `/phonecheck/check/?phone=${phone}&username=${username}`
    //       );
    //       console.log("这是一个手机验证码", data);
    //     }, 5000);
    //   });
    // } else {
    //   alert("请输入手机号码");
    // }
    // },
    Login() {
      this.$router.push("/login");
    },
    async submitForm() {
      this.$refs["ruleForm"].validate(async () => {
        const { yan } = this.ruleForm;
        const { username } = this.ruleForm;
        const { newPass } = this.ruleForm;
        //点击发送请求，获得数据库里存的验证码，如果现在的这个和数据库的里的一样
        //则再发起后面的请求==》修改密码
        const { data } = await this.$request.put("/phonevcode/", {
          yan,
          username,
          newPass,
        });
        console.log("请求修改密码返回来的数据", data);
        if (data.code === 1) {
          alert("恭喜你修改成功!");
          this.$router.push("/login");
        } else if (data.code === 10) {
          alert("验证码不正确!");
        } else {
          alert("修改失败!");
        }
      });
